<template>
  <div>
    <btn ref="upload" cls="is-primary" icon="upload">Upload</btn>
    <div class="button div" @click="showYoutubeUploader">
      <btn cls="is-primary" icon="youtube-square">Youtube Video</btn>
    </div>
    <modal :show="youtubeModalShow">
      <input type="text" value="asdas">
    </modal>
  </div>
</template>

<script>
import Btn from './partials/Btn'
import Modal from './partials/Modal'
import {mapGetters} from 'vuex'
import uploader from '../utils/uploader'

export default {
  name: 'upload',
  components: { Btn, Modal },
  mounted () {
    this.uploader = uploader()
    this.uploader.assignBrowse(this.$refs.upload.$el)
  },
  data () {
    return {
      uploader: false,
      youtubeModal: false
    }
  },
  methods: {
    showYoutubeUploader () {
      this.youtubeModal = true
      console.log(this.youtubeModalShow)
    }
  },
  computed: {
    type () {
      return this.selected.type
    },
    hasSelected () {
      if (!this.selected.type) {
        return false
      }

      return this.selected.type.length > 0
    },
    ...mapGetters([
      'selected'
    ]),
    youtubeModalShow () {
      return this.youtubeModal
    }
  }
}
</script>

<style lang="scss" scoped>
$family-monospace: 'Inconsolata', 'Consolas', 'Monaco', monospace !default;

.nav-left {
  overflow: hidden;
  flex-basis: 75%;
}
.nav-right {
  width: 20%;
}
.tabs {
  li {
    margin-left: 2vw;
    font-size: 80%;
    color: lighten(#333, 90%);
    font-family: $family-monospace;

    &:first-child {
      margin-left: 0;
    }
  }
}
.div.button {
  border: 0;
  padding: 0;
}
</style>



